@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.grid-left{grid-column:1}.grid-right{grid-column:3}.grid-h-center{grid-column:2}.grid-top{grid-row:1}.grid-bottom{grid-row:3}.grid-v-center{grid-row:2}.icons-mobile-primary span *{color:#e0ffff}@media screen and (min-width: 600px){.icons-desktop-primary span *{color:#e0ffff}}.icons-mobile-secondary span *{color:navy}@media screen and (min-width: 600px){.icons-desktop-secondary span *{color:navy}}.hero-component{display:grid;grid-template:1;justify-content:center;align-content:center;position:relative;width:100%;height:100vh}.hero-component.flex-hero{display:flex;flex-direction:column}@media screen and (min-width: 600px){.hero-component{grid-template:repeat(3, 30%)/repeat(3, 30%)}}.hero-component>*{align-self:center;justify-self:center}.hero-component>*:not(.background-video){z-index:2}.hero-component .home-content{color:white;text-align:center}@media screen and (min-width: 600px){.hero-component .home-content{color:black;text-align:left}}.hero-component .about-me-content{display:flex;flex-direction:column;align-items:center;justify-content:space-between}.hero-component .about-me-content .headshot{border-radius:200px;width:50%}.hero-component .about-me-content .about-me-text{padding:2rem;text-align:center}@media screen and (min-width: 600px){.hero-component .about-me-content{flex-direction:row;min-width:50rem}.hero-component .about-me-content img{width:75%}.hero-component .about-me-content .about-me-text{text-align:end;margin-left:5rem}}.hero-component .background-video{position:absolute;width:100%;height:100%;object-fit:cover}.links-component span a{color:#e0ffff;opacity:0.6;text-decoration:none;padding-right:0.75rem}.links-component span a:hover{opacity:1}.links-component span:not(.text-link):hover svg{transform:scale(1.5);transition:transform 0.25s}.card-component{display:flex;flex-direction:column;justify-content:center;width:50%;height:50vh;padding-top:30vh;background-size:contain;background-repeat:no-repeat}.card-component .card-text{display:flex;flex-direction:column;align-items:center;width:100%;margin-top:-12rem;z-index:10;color:white}.card-component .card-text .card-links{display:flex;flex-direction:row;justify-content:space-evenly;padding:1rem}.card-component .card-text .card-links span{text-align:center}footer{display:flex;flex-direction:column;justify-content:center;align-items:center;padding-top:7rem;background:linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0.6));color:white}.copyright{margin:0;padding:1rem 0;opacity:.8}.carousel-component{position:relative;display:flex}.carousel-component .viewport-wrapper{overflow:hidden}.carousel-component .carousel-viewport{position:relative;white-space:nowrap;overflow-x:auto;overflow-y:hidden;-ms-scroll-snap-type:x mandatory;scroll-snap-type:x mandatory;scroll-behavior:smooth;-webkit-overflow-scrolling:touch;-ms-overflow-style:none;scrollbar-width:none}.carousel-component .carousel-viewport.no-scroll-animation{scroll-behavior:auto}.carousel-component .carousel-viewport::-webkit-scrollbar{display:none}.carousel-component .slide{white-space:normal;position:relative;vertical-align:top;display:inline-flex;flex-direction:column;justify-content:center;align-items:center;cursor:pointer;scroll-snap-align:start;width:100%}.carousel-component .carousel-nav{position:absolute;top:0;cursor:pointer;outline:none;z-index:4;align-items:center;justify-content:center}.carousel-component .carousel-nav.nav-left{left:20vw}.carousel-component .carousel-nav.nav-right{right:20vw}#app{font-family:'Spartan', sans-serif;color:black;background-image:url(/brandonrowe-portfolio-v2/static/media/textured-paper.2e582823.png);position:relative;width:100%;overflow:hidden}

